<template>
    <div class="resumeEditor" :class="{htmlMode:enableHtml}" ref="container">
        <div v-if="enableHtml" v-html="myResume"></div>
        <pre v-else>{{myResume}}</pre>
    </div>
</template>
<script>
import marked from 'marked'
export default {
    name: 'resumeEditor',
    props: ['enableHtml', 'markdown'],
    computed: {
        myResume: function() {
            return this.enableHtml ? marked(this.markdown) : this.markdown
        }
    },
    methods: {
        goBottom: function() {
            this.$refs.container.scrollTop = 100000
        }
    }
}
</script>
<style scoped>
.htmlMode {
    animation: flip 2s;
}

@keyframes flip {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
</style>



